.friendMessage {
    min-height: 77px;
}

.friendImageAndText {
    display: flex;
    justify-content: start;
    align-items: center;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
}

.friendText {
    background: #F5F7FB;
    margin-right: 12px;
    border-radius: 10px;
    color: black;
    padding: 10px 15px;
}

.friendText:after {

}

.friendName {
    text-align: start;
    font-family: Montserrat, serif;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
}

.friendMessageText {
    font-family: Montserrat, serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
}

.friendTime {
    text-align: start;
    font-size: 10px;
    padding-left: 10px;

}
