.message {
    min-height: 77px;

}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    justify-content: end;
    align-items: center;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
    background: #0066CC;
    margin-right: 12px;
    border-radius: 10px;
    color: white;
    padding: 10px 15px;
}

.text:after {

}

.name {
    text-align: end;
    font-family: Montserrat, serif;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
}

.messageText {
    font-family: Montserrat, serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
}

.time {
    text-align: end;
    font-size: 10px;
    padding-right: 10px;


}